.home {
  height: 90vh;
  display: flex;
  justify-content: center;
  align-items: left;
  flex-direction: column;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.headercontainer {
  width: auto;
  margin-left: 30px;
}

.home .headercontainer h1 {
  justify-content: center;
  align-items: center;
  font-weight: bold;
  font-size: 70px;
  font-family: serif;
  color: rgb(96, 250, 6);
}

.home .headercontainer p {
  margin-top: 15px;
  padding: 0;
  font-size: 40px;
  font-weight: lighter;
  color: black;
}

.headercontainer button {
  background-color: black;
  margin-top: 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  height: 50px;
  width: 180px;
  color: white;
  text-decoration: none;
}
.headercontainer button:hover {
  background-color: #3437da;
  color: white;
  cursor: pointer;
  transition: 0.3s ease-in;
}

@media only screen and (max-width: 600px) {
  .home {
    justify-content: center;
    align-items: center;
  }
  .headercontainer {
    margin-left: 0;
    border-radius: 10px;
    width: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: #121619;
    color: white;
  }
  .home .headercontainer h1 {
    font-size: 40px;
    height: 30px;
    margin: 15px 0px;
    color: white;
  }
  .home .headercontainer p {
    font-size: 30px;
    color: white;
  }
  .headercontainer button {
    background-color: white;
    color: black;
    margin-bottom: 30px;
  }
}
